/* Stylesheet for the navlinks */

nav {
    width: 50%;
    max-width: 1000px;
    margin: 0 auto;
    padding-top: 3em;
    background-color: #f3f3f3;
}  

.nav-title {
    font-family: 'Cutive Mono', 'Roboto', monospace, sans-serif;
    font-size: 2.5em;
}

.nav-link {
    margin-right: 2em;
    display: inline-block;
}

.nav-link, .nav-title {
    color: black;
    text-decoration: none;
}

.nav-right {
    float: right;
    padding-top: 0.8em;
}

.nav-right .nav-link {
    padding: 0 1em;
    padding-bottom: 0.2em;
    text-align: center;
}

@media screen and (max-width: 999px){ /* smaller screens */
    #navbar {
        padding-bottom: 1em;
    }
    
    nav {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    .nav-right {
        float: none;
    }
}

@media screen and (max-width: 721px){ /* smaller screens */
    .nav-right {
        display: flex;
        flex-direction: column;
        margin-bottom: 0;
    }

    .nav-right .nav-link {
        margin: 0.2em;
    }
}