/* Keywords */
ul {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
  
.keywords li{
    display: inline;
    color: #fff;
    background: #14213D;
    font-family: monospace;
    list-style-type: circle;
    padding: 0.2em 1em;
    border-radius: 1em;
    margin: 0.2em;
    white-space: nowrap;
}

.project-grid {
    width: 80%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}

.q-one {
    grid-column: 1/2;
}

.q-two {
    grid-column: 2/3;
}

.q-three {
    grid-column: 3/4;
}

.q-four {
    grid-column: 4/5;
}

.page-left {
    grid-column: 1/3;
}

.page-center {
    grid-column: 2/4;
}

.page-right {
    grid-column: 3/5;
}